body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.btn, .btn-primary {
  border: 1px solid #409254;
  background-color: #28a745; }
  .btn:focus, .btn-primary:focus {
    box-shadow: 0 0 0 0.1rem #87ca98 !important; }
  .btn:active, .btn-primary:active {
    box-shadow: 0 0 0 0.1rem #52b36a !important; }
  .btn:hover, .btn:active, .btn-primary:hover, .btn-primary:active {
    color: #343a40 !important;
    border-color: #409254 !important; }

body {
  width: 100%;
  height: auto;
  /*background: -webkit-linear-gradient(0deg, #3c96ff 0%, #2dfbff 100%);*/
  background: #28a745;
  font-family: "Robato",sans-serif;
  font-size: 17px; }

#logreg-forms {
  width: 412px;
  margin: 10vh auto;
  background-color: #ffffff4d;
  box-shadow: 0 7px 7px rgba(0, 0, 0, 0.12), 0 12px 40px rgba(0, 0, 0, 0.24);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1); }
  #logreg-forms form {
    width: 100%;
    max-width: 410px;
    padding: 15px;
    margin: auto; }
  #logreg-forms .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px; }
  #logreg-forms .form-control:focus {
    z-index: 2;
    border-color: #409254; }
  #logreg-forms .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; }
  #logreg-forms .form-signin input[type="password"] {
    border-top-left-radius: 0;
    border-top-right-radius: 0; }
  #logreg-forms .form-signup .social-login {
    width: 210px !important;
    margin: 0 auto; }
  #logreg-forms .social-login {
    width: 390px;
    margin: 0 auto;
    margin-bottom: 14px; }
  #logreg-forms .social-btn {
    font-weight: 100;
    color: white;
    width: 190px;
    font-size: 0.9rem; }
  #logreg-forms a {
    display: block;
    padding-top: 10px;
    color: #3C589C; }
  #logreg-forms .lines {
    width: 200px;
    border: 1px solid red; }
  #logreg-forms button[type="submit"] {
    margin-top: 10px;
    background-color: #28a745; }
  #logreg-forms .facebook-btn {
    background-color: #3C589C; }
  #logreg-forms .google-btn {
    background-color: #DF4B3B; }
  #logreg-forms .form-reset, #logreg-forms .form-signup {
    display: none; }
  #logreg-forms .form-signup .social-btn {
    width: 210px; }
  #logreg-forms .form-signup input {
    margin-bottom: 2px; }

.submit {
  background-color: #28a745;
  border-radius: 25px;
  color: #fff; }

#btn-signup {
  background-color: #28a745; }

/* Mobile */
@media screen and (max-width: 500px) {
  #logreg-forms {
    width: 300px; }
  #logreg-forms .social-login {
    width: 200px;
    margin: 0 auto;
    margin-bottom: 10px; }
  #logreg-forms .social-btn {
    font-size: 1.3rem;
    font-weight: 100;
    color: white;
    width: 200px;
    height: 56px; }
  #logreg-forms .social-btn:nth-child(1) {
    margin-bottom: 5px; }
  #logreg-forms .social-btn span {
    display: none; }
  #logreg-forms .facebook-btn:after {
    content: 'Facebook'; }
  #logreg-forms .google-btn:after {
    content: 'Google+'; } }

